.search-container {
  justify-content: center;
  margin: 20px 0;
  display: flex;
}

#search-input {
  width: 200px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

#search-button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

#search-button:hover {
  background-color: #0056b3;
}

#weather-data {
  text-align: center;
  margin-top: 20px;
}

#weather-data img {
  width: 50px;
  height: 50px;
}

.weather-card {
  width: 100%;
  background-color: #102136;
  border-radius: 35px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
  padding: 30px 0 38px;
  display: flex;
  position: absolute;
  right: 0;
  box-shadow: 0 8px 43px rgba(16, 33, 54, .59);
}

.city-name {
  color: rgba(255, 255, 255, .3);
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 1.375;
}

.temperature-card {
  color: #fff;
  margin: 10px 0;
  font-size: 2em;
}

.divide-line {
  margin: 0 10px;
}

.description {
  color: #fff;
}

.min-temp, .max-temp, .humidity {
  color: rgba(255, 255, 255, .3);
}

.min-temp, .max-temp {
  margin-bottom: 0;
}

.min-text, .max-text {
  color: rgba(255, 255, 255, .3);
  flex-direction: column;
  align-items: center;
  display: flex;
}

.min, .max {
  padding: 0 10px;
  display: flex;
}

.min {
  border-right: 2px solid rgba(255, 255, 255, .3);
}

.divide-line {
  height: 40px;
}

.temps-divide {
  display: flex;
}

@media (min-width: 768px) {
  .weather-card {
    width: 492px;
  }
}



